import React, { useState, useEffect } from 'react';
import { Input, Button, message, Spin, Divider, Space, Typography } from 'antd';
import { extractXiaohongshuInfo } from './services/xiaohongshuService';
import { XiaohongshuInfo, Comment } from './types/xiaohongshu';
//  SDK
import { bitable, FieldType } from '@lark-base-open/js-sdk';

// 
type ITable = any;

const { Title, Paragraph } = Typography;

interface RequiredField {
  name: string;
  type: typeof FieldType[keyof typeof FieldType]; //  FieldType 
  property?: Record<string, any>;
  isMultiline?: boolean;
}

const App: React.FC = () => {
  const [url, setUrl] = useState<string>('');
  const [loading, setLoading] = useState<boolean>(false);
  const [noteInfo, setNoteInfo] = useState<XiaohongshuInfo | null>(null);
  const [table, setTable] = useState<ITable | null>(null);
  const [processing, setProcessing] = useState<boolean>(false);

  // 
  useEffect(() => {
    const initTable = async () => {
      try {
        //  API  bitable API
        const selection = await bitable.base.getSelection();
        if (selection.tableId) {
          const activeTable = await bitable.base.getTableById(selection.tableId);
          setTable(activeTable);
        } else {
          message.warning('');
        }
      } catch (error) {
        console.error(':', error);
        message.error('');
      }
    };

    initTable();
  }, []);

  const handleUrlChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setUrl(e.target.value);
  };

  const extractUrl = (text: string): string | null => {
    // 
    const regex = /(https?:\/\/(?:www\.)?xiaohongshu\.com\/(?:discovery\/item|explore)\/(\w+))/i;
    const match = text.match(regex);
    return match ? match[1] : null;
  };

  const handleExtract = async () => {
    const extractedUrl = extractUrl(url);
    if (!extractedUrl) {
      message.error('');
      return;
    }

    setLoading(true);
    try {
      const info = await extractXiaohongshuInfo(extractedUrl);
      setNoteInfo(info);
      message.success('');
    } catch (error) {
      console.error(':', error);
      message.error('');
    } finally {
      setLoading(false);
    }
  };

  const handleSaveToTable = async () => {
    if (!noteInfo || !table) {
      message.error('');
      return;
    }

    setProcessing(true);
    try {
      // 
      await ensureFields(table);

      // 
      await table.addRecord({
        fields: {
          '': url,
          '': noteInfo.title,
          '': noteInfo.author,
          '': noteInfo.publishTime,
          '': noteInfo.content,
          '': noteInfo.tags.join(','),
          '': [{ type: 'url', value: noteInfo.coverImage }],
          '': noteInfo.comments.map((c: Comment) => `${c.user}: ${c.content}`).join('\n')
        }
      });

      message.success('');
    } catch (error) {
      console.error(':', error);
      message.error('');
    } finally {
      setProcessing(false);
    }
  };

  const ensureFields = async (table: ITable) => {
    // 
    const fields = await table.getFieldMetaList();
    const fieldMap = new Map(fields.map((field: any) => [field.name, field]));

    // 
    const requiredFields: RequiredField[] = [
      { name: '', type: FieldType.Text },
      { name: '', type: FieldType.Text },
      { name: '', type: FieldType.Text },
      { name: '', type: FieldType.DateTime },
      { name: '', type: FieldType.Text, isMultiline: true },
      { name: '', type: FieldType.Text },
      { name: '', type: FieldType.Attachment },
      { name: '', type: FieldType.Text, isMultiline: true }
    ];

    // 
    for (const field of requiredFields) {
      if (!fieldMap.has(field.name)) {
        if (field.type === FieldType.Text && field.isMultiline) {
          await table.addField({
            type: field.type,
            name: field.name,
            property: {
              defaultValue: '',
              formatter: 'none',
              multiline: true
            }
          });
        } else {
          await table.addField({
            type: field.type,
            name: field.name
          });
        }
      }
    }
  };

  return (
    <div className="app-container">
      <Title level={3}></Title>
      <Paragraph></Paragraph>
      
      <div className="section">
        <Space direction="vertical" style={{ width: '100%' }}>
          <Input.TextArea 
            value={url} 
            onChange={handleUrlChange} 
            placeholder="" 
            rows={3} 
          />
          <Button 
            type="primary" 
            onClick={handleExtract} 
            loading={loading}
            block
          >
            
          </Button>
        </Space>
      </div>

      {loading && (
        <div className="loading-container">
          <Spin tip="..." />
        </div>
      )}

      {noteInfo && !loading && (
        <div className="result-container">
          <Title level={4}></Title>
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <img src={noteInfo.coverImage} alt="" className="preview-image" />
          </div>
          
          <Divider style={{ margin: '12px 0' }} />
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <div className="result-item-content">{noteInfo.title}</div>
          </div>
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <div className="result-item-content">{noteInfo.author}</div>
          </div>
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <div className="result-item-content">{noteInfo.publishTime}</div>
          </div>
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <div className="result-item-content">{noteInfo.content}</div>
          </div>
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <div className="tag-list">
              {noteInfo.tags.map((tag: string, index: number) => (
                <span key={index} className="tag">{tag}</span>
              ))}
            </div>
          </div>
          
          <div className="result-item">
            <div className="result-item-title"></div>
            <div className="comment-list">
              {noteInfo.comments.map((comment: Comment, index: number) => (
                <div key={index} className="comment-item">
                  <div className="comment-user">{comment.user}</div>
                  <div className="comment-content">{comment.content}</div>
                </div>
              ))}
            </div>
          </div>
          
          <Divider style={{ margin: '16px 0' }} />
          
          <Button 
            type="primary" 
            onClick={handleSaveToTable} 
            loading={processing}
            block
          >
            
          </Button>
        </div>
      )}
    </div>
  );
};

export default App;